.datepicker--cell.-current-.-in-range- {
  color: #fff; 
  background: $color-tertiary-gradient;
}
.datepicker--cell-day.-range-to- {
  border: none;
  background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
  border-radius: 22px;
  &::before{
    content: "";
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    position: absolute;
    background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
    border-radius: 0px 22px 22px 0;
    z-index: -2;
  }
}
.datepicker--cell-day.-range-from- {
  border: none;
  background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
  border-radius: 22px;
  &::before{
    content: "";
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    position: absolute;
    background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
    border-radius: 22px 0 0 22px;
    z-index: -2;
  }
}
.datepicker--cell-day.-range-from-.-range-to- {
  border-radius: 4px; }
.datepicker--cell-day.-selected- {
  color: #fff;
  border: none;
  background: $color-secondary-gradient; 
  font-weight: bold;
}
.datepicker--cell-day.-selected-.-focus- {
  background: $color-secondary-gradient; 
}
.datepicker--cell-day.-in-range- {
  background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
  color: $color-main-50;
  border-radius: 0; 
}
.-in-range-.datepicker--cell-day.-other-month-, .-in-range-.datepicker--cell-year.-other-decade- {
  background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
  color: $color-main-25; 
}

/* Месяцы */

.datepicker--cell-month.-current-.-range-from- {
  background: $color-tertiary-gradient;
}
.datepicker--cell-month.-current-.-range-to- {
  background: $color-tertiary-gradient;
}
.datepicker--cell-month.-in-range- {
  background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
  color: $color-main-50;
  border-radius: 0; 
}
.datepicker--cell-month.-range-to- {
  border: none;
  background: linear-gradient(180deg, darken(rgba(188,158,255,0.25), 10%) 0%, darken(rgba(139,164,249,0.25), 10%) 100%);
}
.datepicker--cell-month.-range-from- {
  border: none;
  background: linear-gradient(180deg, darken(rgba(188,158,255,0.25), 10%) 0%, darken(rgba(139,164,249,0.25), 10%) 100%);
}
.datepicker--cell-month.-selected-.-focus- {
  background: $color-secondary-gradient; 
}

/* Годы */


.datepicker--cell-year.-current-.-range-from- {
  background: $color-tertiary-gradient;
}
.datepicker--cell-year.-current-.-range-to- {
  background: $color-tertiary-gradient;
}
.datepicker--cell-year.-in-range- {
  background: linear-gradient(180deg, rgba(188,158,255,0.25) 0%, rgba(139,164,249,0.25) 100%);
  color: $color-main-50;
  border-radius: 0; 
}
.datepicker--cell-year.-range-to- {
  border: none;
  background: linear-gradient(180deg, darken(rgba(188,158,255,0.25), 10%) 0%, darken(rgba(139,164,249,0.25), 10%) 100%);
}
.datepicker--cell-year.-range-from- {
  border: none;
  background: linear-gradient(180deg, darken(rgba(188,158,255,0.25), 10%) 0%, darken(rgba(139,164,249,0.25), 10%) 100%);
}
.datepicker--cell-year.-selected-.-focus- {
  background: $color-secondary-gradient; 
}