@import '../dropdown/dropdown.scss';
@import '../date-dropdown/date-dropdown.scss';
@import '../calendar/calendar.scss';
@import '../submit-button/submit-button.scss';

.search-room-card{
  background-color: white;
  padding: 40px 30px 30px 30px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  border-radius: 4px;
  @media (max-width: 360px) {
    padding: 40px 4.33vw 30px 4.33vw;
  }
  &__title{
    font-family: $font-secondary-reserve;
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 20px;
    line-height: 1.875rem;
  }
  &__date-dropdown{
    margin-bottom: 20px;
  }
  &__dropdown{
    margin-bottom: 31px;
  }
}